/* Gestion des Tags */
.content-tags {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  
  .tag {
    background-color: #ff6060;
    color: white;
    padding: 5px 10px;
    border-radius: 10px;
    font-size: 14px;
    text-align: center;
    display: flex; 
    justify-content: center; 
    align-items: center; 
    flex: 1; 
    max-width: 150px; 
    min-width: 100px; 
    white-space: nowrap;
  }

  @media (max-width: 768px) {
    .content-tags {
      gap: 5px;
    }
    .tag {
      max-width: 80px;
      font-size: 10px;
      padding: 5px 1px;
    }
  }